body, html, #root, div {
    overflow-x: hidden;
}

ul {
    list-style: none;
    width: 100%;
    box-sizing: border-box;
    margin: 50px auto;
}

ul a {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

ul a li {
    width: 25vw;
}